import React, {Component} from "react";


export const Banner = ({showStep}) => {

    return (<>
            {showStep >= 5 ? <></> :
                <div className=" givingBanner ">
                    <h1 className="row givingBanner__title title">Wane! </h1>
                    {(showStep === 1) &&
                    <p className="row givingBanner__text"> Uzupenij szczegy dotyczce Twoich rzeczy. Dziki temu
                        bdziemy
                        wiedzie komu najlepiej je przekaza.</p>}
                    {(showStep === 2) &&
                    <p className="row givingBanner__text">Wszystkie rzeczy do oddania zapakuj w 60l worki. Dokadn
                        instrukcj jak poprawnie spakowa rzeczy znajdziesz <a href="#"> TUTAJ</a>. </p>}
                    {(showStep === 3) &&
                    <p className="row givingBanner__text">Jeli wiesz komu chcesz pomc, moesz wpisa nazw tej
                        organizacji
                        w wyszukiwarce. Moesz te filtrowa organizacje po ich lokalizacji bd celu ich pomocy. </p>}
                    {(showStep === 4) &&
                    <p className="row givingBanner__text">Podaj adres oraz termin odbioru rzeczy. </p>}
                </div>
            }

        </>
    )
}